import React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import DateUtility from './DateUtility';
import { ThemeProvider, createTheme } from '@mui/material/styles';

const CharacterCard = ({ character }) => {
  const { image, status, species, gender, origin, location, name, id, created } = character;

  const theme = createTheme({
    typography: {
      fontFamily: 'Arvo',
    },
  });

  return (
    <ThemeProvider theme={theme}>
    <Card
      sx={{
        maxWidth: 345,  
        margin: 'auto',
        backgroundColor: '#2e2e2e',
        color: '#fff',
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        position: 'relative',
        borderRadius:'10px'
      }}
    >
      <Box sx={{ position: 'relative' }}>
        <CardMedia
          component="img"
          height="196" // Increased height to approximate 70% of the total card height
          image={image}
          alt={name}
          sx={{
            objectFit: 'cover',
            maxHeight: 196,
            filter: 'brightness(0.8)',
          }}
        />
        <Box
          sx={{
            position: 'absolute',
            bottom: 0,
            width: '100%',
            backgroundColor: 'rgba(0, 0, 0, 0.6)', // Semi-transparent black background
            color: '#fff',
            padding: '8px',
            textAlign: 'center',
            height: '58px', // Adjusted height for 30% overlay
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center'
          }}
        >
          <Typography variant="subtitle1" component="div" sx={{ fontWeight: 'bold' }}>
            {name}
          </Typography>
          <Typography variant="body2" component="div">
            id: {id} - <DateUtility created={created} />
          </Typography>
        </Box>
      </Box>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 2 }}>
          <Typography variant="body2" color="#fff">
            Status
          </Typography>
          <Typography variant="body2" color="text.primary" sx={{ color: '#ff9800' }}>
            {status}
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 1 }}>
          <Typography variant="body2" color="#fff">
            Species
          </Typography>
          <Typography variant="body2" color="text.primary" sx={{ color: '#ff9800' }}>
            {species}
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 1 }}>
          <Typography variant="body2" color="#fff">
            Gender
          </Typography>
          <Typography variant="body2" color="text.primary" sx={{ color: '#ff9800' }}>
            {gender}
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 1 }}>
          <Typography variant="body2" color="#fff">
            Origin
          </Typography>
          <Typography variant="body2" color="text.primary" sx={{ color: '#ff9800' }}>
            {origin.name}
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 1 }}>
          <Typography variant="body2" color="#fff">
            Last Location
          </Typography>
          <Typography variant="body2" color="text.primary" sx={{ color: '#ff9800' }}>
            {location.name}
          </Typography>
        </Box>
      </CardContent>
    </Card>
    </ThemeProvider>
  );
};

export default CharacterCard;