import React, { useEffect, useState, useCallback } from 'react';
import axios from 'axios';
import { Grid, Box } from '@mui/material';
import CharacterCard from './CharacterCard';
import CharacterSearch from './CharacterSearch';
import CharacterSort from './CharacterSort';
import CharacterFilter from './CharacterFilter';
import SelectedFilters from './SelectedFilters';

const CharacterList = () => {
  const [characters, setCharacters] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortOrder, setSortOrder] = useState('asc');
  const [filterOptions, setFilterOptions] = useState({
    species: [],
    gender: [],
    origin: []
  });
  const [filters, setFilters] = useState({
    species: [],
    gender: [],
    origin: []
  });

  useEffect(() => {
    axios.get('https://rickandmortyapi.com/api/character')
      .then(response => {
        setCharacters(response.data.results);
        extractFilterOptions(response.data.results);
      })
      .catch(error => {
        console.error('Error fetching data:', error);
      });
  }, []);

  const extractFilterOptions = (data) => {
    const uniqueSpecies = [...new Set(data.map(c => c.species))];
    const uniqueGender = [...new Set(data.map(c => c.gender))];
    const uniqueOrigins = [...new Set(data.map(c => c.origin.name))];
    setFilterOptions({ species: uniqueSpecies, gender: uniqueGender, origin: uniqueOrigins });
  };

  const handleSearchChange = (searchValue) => {
    setSearchTerm(searchValue);
  };

  const handleSortChange = (newSortOrder) => {
    setSortOrder(newSortOrder);
  };

  const handleFilterChange = useCallback((newFilters) => {
    setFilters(newFilters);
  }, []);

  // const handleRemoveFilter = useCallback((filterType, filterValue) => {
  //   setFilters(prevFilters => ({
  //     ...prevFilters,
  //     [filterType]: prevFilters[filterType].filter(val => val !== filterValue)
  //   }));
  // }, []);

  const sortedCharacters = [...characters].sort((a, b) => {
    return sortOrder === 'asc' ? a.id - b.id : b.id - a.id;
  });

  const filteredCharacters = sortedCharacters.filter(character =>
    character.name.toLowerCase().includes(searchTerm.toLowerCase()) &&
    (filters.species.length === 0 || filters.species.includes(character.species)) &&
    (filters.gender.length === 0 || filters.gender.includes(character.gender)) &&
    (filters.origin.length === 0 || filters.origin.includes(character.origin.name))
  );

  return (
    <Box sx={{ padding: 2 }}>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={12} md={3}>
          <CharacterFilter
            speciesOptions={filterOptions.species}
            genderOptions={filterOptions.gender}
            originOptions={filterOptions.origin}
            onFilterChange={handleFilterChange}
          />
        </Grid>
        <Grid item xs={12} sm={12} md={9}>
          <Box sx={{ mb: 2 }}>
            {/* <SelectedFilters filters={filters} onRemoveFilter={handleRemoveFilter} /> */}
          </Box>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} sm={12}  sx={{
            display: 'flex',
            flexDirection: { xs: 'column', sm:'row' }, // Column on xs (mobile), row on sm (tablet) and up
            gap: 3,
            justifyContent: 'space-between',
          }}
            >
              <CharacterSearch value={searchTerm} onChange={handleSearchChange} />
              <CharacterSort sortOrder={sortOrder} onChange={handleSortChange} />
            </Grid>
          </Grid>
          <Grid container spacing={2} mt={2}>
            {filteredCharacters.map(character => (
              <Grid item xs={12} sm={6} md={4} lg={3} key={character.id}>
                <CharacterCard character={character} />
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Grid>
    </Box>
  );
};

export default CharacterList;
